@font-face {
  font-family: "Roboto";
  src: local("Roboto-Black"), url(../fonts/Roboto-Black.ttf) format("truetype");
}

.container-title {
  position: relative;
  color: white;
}

.title {
  position: absolute;
  width: 256px;
  height: 41px;
  left: 67px;
  top: 56px;

  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 35px;
  line-height: 41px;
  white-space: nowrap;
}

.image-title {
  width: 100%;
}

.button-vision {
  position: absolute;
  width: 170px;
  height: 69px;
  right: 500px;
  top: 62px;

  color: white;
  background: #bc1823;
  border-radius: 1px;
  border: none;

  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 25px;
  line-height: 29px;
  cursor: pointer;
}
.button-vision:hover {
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

